import React, { PureComponent } from 'react';
import { Link as ScrollLink } from 'react-scroll';
import isFunction from 'lodash/isFunction';
import pages from '../pages.json';

import Box from '../../components/Box';
import Text from '../../components/Text';
import Flex from '../../components/Flex';

class Option extends PureComponent {
  state = {
    nowPage: false
  };

  setActive = (value) => {
    const { closeMenu } = this.props;
    this.setState({ focus: value });
    if (isFunction(closeMenu)) closeMenu();
  }

  render() {
    const { isMobile, ...props } = this.props;
    const { nowPage } = this.state;
    return (
      <Flex flexWrap={isMobile ? 'wrap' : 'no-wrap'} flexDirection={isMobile ? 'column' : 'row'} {...props}>
        {pages.map((page, index) => {
          const active = nowPage === index;
          return (
            <Flex key={index} mb={isMobile ? '0.5em' : '0'} justifyContent={isMobile ? 'center' : 'flex-start'}>
              <ScrollLink
                smooth
                spy
                to={page.to}
                onSetActive={() => this.setState({ nowPage: index })}
                offset={-80}
              >
                <Box
                  color={active ? 'black' : 'gray'}
                  onClick={() => this.setActive(index)}
                  borderBottom="3px solid"
                  borderColor={active ? 'yellow' : 'transparent'}
                  pb="0.25em"
                  fontWeight={active ? 'bold' : 'normal'}
                  fontSize={['0.8em', null, '1em']}
                >
                  {page.label}
                </Box>
              </ScrollLink>
            {!isMobile && index < pages.length - 1 && (<Text color="gray" mx="0.5em" lineHeight="1">|</Text>)}
            </Flex>
          );
        })}
      </Flex>
    );
  }
}

export default Option;
