import React, { PureComponent } from 'react'
import { Link as ScrollLink } from 'react-scroll';
import { slide as Menu } from 'react-burger-menu'
import { MdMenu } from 'react-icons/md';

import headerlogo from './headerlogo.svg';
import Option from './Option';

import FreezeScroll from '../../utils/FreezeScroll';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import BackgroundImage from '../../components/BackgroundImage';
import theme from '../../components/ThemeProvider/theme';
import Text from '../../components/Text';
import withResponsive from '../../hoc/withResponsive';

const frezzer = new FreezeScroll()

class Header extends PureComponent {
  state = {
    nowPage: false
  };

  // componentDidUpdate(prevProps, prevState) {
  //   const { isOpen } = this.state;
  //   if (isOpen !== prevState.isOpen) {
  //     if (isOpen) {
  //       frezzer.freeze();
  //     } else {
  //       frezzer.unfreeze();
  //     }
  //   }
  // }

  toggleOpen = () => this.setState(({ isOpen }) => ({ isOpen: !isOpen }))

  closeMenu = () => this.setState({ isOpen: false })

  render() {
    const { isOpen } = this.state;
    const { isMobile } = this.props;
    return (
      <div>
        <Flex
          bg="white"
          position="fixed"
          left="0"
          right="0"
          justifyContent="space-between"
          px="1em"
          py="1em"
          height={theme.headerHeight}
          alignItems="center"
          zIndex={99}
          boxShadow="0px 4px 6px rgba(0,0,0,0.15)"
        >
          <Box maxWidth="5em" minWidth="3em" width="8%">
            <ScrollLink
              spy
              smooth
              to="intro"
              onSetActive={() => this.setState({ nowPage: 'intro' })}
              style={{ cursor: 'pointer' }}
            >
              <BackgroundImage src={headerlogo} ratio={71.95 / 126.41} />
            </ScrollLink>
          </Box>
          <Box>
            {isMobile ? (
              <Box onClick={this.toggleOpen} >
                <MdMenu size="2em" />
              </Box>
            ) : (
              <Flex>
                <Option isMobile={isMobile} />
              </Flex>
            )}
          </Box>
        </Flex>
        <Box
          position="fixed"
          right="0"
          left="0"
          px="1em"
          top={theme.headerHeight}
          transform={isOpen ? 'translateY(0)' : 'translateY(-100%)'}
          pt="2em"
          pb="1em"
          zIndex={3}
          bg="white"
          transition="transform 500ms"
        >
          <Option fontSize="1.2em" isMobile={isMobile} closeMenu={this.closeMenu} />
        </Box>
        <Box
          position="fixed"
          top="0"
          right="0"
          left="0"
          bottom="0"
          bg="black"
          opacity={isOpen ? '0.5' : '0'}
          zIndex={2}
          transition="opacity 500ms"
          pointerEvents={!isOpen && 'none'}
        />
      </div>
    )
  }
};

export default withResponsive(Header);
